<template>
  <a-row class="mt-40" :gutter="40">
    <a-col style="border: 1px solid transparent" :span="2"></a-col>
    <a-col :span="20">
<!--      head-->
      <a-row class="mt-20 sub-gray-line" :gutter="20" style="height: 200px;">
        <a-col :span="6" style="height: 100%; max-width: 100%; display: flex; justify-content: center">
          <img v-if="itemBaseInfo.coverPicUuid" style="max-width: 100%; max-height: 100%" :src="picBaseURL + itemBaseInfo.coverPicUuid"/>
          <img v-else style="width: 100%; height: 100%" src="../../../../../static/imgs/default-img.jpeg"/>
        </a-col>
        <a-col :span="18" style="height: 100%;">
          <a-col>
            <a-col :span="16">
              <h2>
                {{ itemBaseInfo.title }}
              </h2>
            </a-col>
            <a-col class="info-desc-content" style="text-align: right" :span="8">
              <p style="margin-bottom: 0">
                {{ itemBaseInfo.updatedAt }}
              </p>
              <p>
                300k
              </p>
            </a-col>
          </a-col>
          <a-row class="can-not-select" :gutter="20" style="width: 100%; position: absolute; bottom: 0">
            <a-col style="font-size: 18px; line-height: 1.8">
              <a-col>
                :
                <img v-for="i in (Math.floor(itemBaseInfo.recommendation / 2))" :key="'full_star' + i" src="../../../../../static/imgs/fullstar.png"/>
                <img v-if="itemBaseInfo.recommendation % 2" src="../../../../../static/imgs/halfstar.png"/>
                <img v-for="i in (Math.floor((10 - itemBaseInfo.recommendation) / 2))" :key="'un_star' + i" src="../../../../../static/imgs/unstar.png"/>
                <div style="float: right">
                  <a-button type="primary" :loading="loading" v-show="!itemBaseInfo.liked" @click="likeResource"><a-icon type="folder"/></a-button>
                  <a-button :loading="loading" v-show="itemBaseInfo.liked" @click="likeResource"><a-icon type="folder"/></a-button>
                  <a-button @click="showConsultModal" :loading="loading" type="primary"></a-button>
                </div>
              </a-col>
            </a-col>
          </a-row>
        </a-col>
      </a-row>

      <a-row>
        <a-col class="right-gray-line" :span="18">
          <a-col class="mt-10" style="text-align: center">
            <span :class="'clickable-txt ' + (selectedTag === 'basic' ? 'selected' : '')" @click="selectedTag = 'basic'"></span>
            <span :class="'clickable-txt ' + (selectedTag === 'review' ? 'selected' : '')" @click="selectedTag = 'review'"></span>
            <span :class="'clickable-txt ' + (selectedTag === 'more' ? 'selected' : '')" @click="selectedTag = 'more'"></span>
          </a-col>

          <div v-show="selectedTag === 'basic'">

            <div style="float: right; margin-right: 20px">
              <div>
                logo
              </div>
              <div>
                <div v-if="(form.brandLogoList && form.brandLogoList.length)" @click="handlePreview({uuid: form.brandLogoList[0].response.data.uuid})" class="upload-add" style="display: flex; justify-content: center">
                  <img style="max-width: 100px; max-height: 100px; padding: 8px" :src="picBaseURL + form.brandLogoList[0].response.data.uuid" />
                </div>
                <div v-else>
                  
                </div>
              </div>
            </div>

            <a-col class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <span>{{ (form.itemTypeList && form.itemTypeList.length) ? form.itemTypeList.join("/") : '' }}</span>
            </a-col>

            <a-col class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <span>{{ cooperationFormListComputed }}</span>
            </a-col>

            <a-col class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <span>{{ (form.functionOrClassList && form.functionOrClassList.length) ? form.functionOrClassList.join("/") : '' }}</span>
            </a-col>

            <a-row class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <a-col :span="12">
                <a-col>
                  <span>{{ form.brand ? form.brand : '' }}</span>
                </a-col>
              </a-col>
            </a-row>

            <a-col class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              {{ form.operationExperienceIntroduction ? form.operationExperienceIntroduction : '' }}
            </a-col>

            <a-col v-if="form.operationCase && form.operationCase.status" class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <div v-if="form.operationCase && form.operationCase.status">
              <div class="gray-board" style="padding: 10px 20px; border-bottom-left-radius: 0; border-bottom-right-radius: 0">
                <span v-if="(!form.operationCase.cases || form.operationCase.cases.length === 0)" class="gray-font" style="font-size: 15px; font-weight: normal"></span>
                <span v-for="(_case, index) of form.operationCase.cases" :key="'operationCase' + index" style="font-size: 15px; margin-right: 20px">
                  <span @click="showCase(index)" :class="showCaseIndex === index ? 'blue' : 'clickable-txt'">{{ _case.title ? _case.title : '' + (index + 1) }} </span>
                </span>
              </div>

                <div v-if="showCaseIndex !== null" style="border: solid 1px #e8e8e8; border-bottom-left-radius: 5px; border-bottom-right-radius: 5px; padding: 10px 20px">

              <a-row class="mt-10">
                <a-col :span="12">
                  <img v-if="(form.operationCase.cases[showCaseIndex].coverPicList && form.operationCase.cases[showCaseIndex].coverPicList.length)" style="width: 80px; height: 80px" :src="picBaseURL + form.operationCase.cases[showCaseIndex].coverPicList[0].response.data.uuid"/>
                </a-col>
              </a-row>

              <a-row class="mt-10">
                <span class="input-tag">: </span>
                {{ form.operationCase.cases[showCaseIndex].title ? form.operationCase.cases[showCaseIndex].title : '' }}
              </a-row>

              <a-row class="mt-10">
                <span class="input-tag">: </span>
                {{ (form.operationCase.cases[showCaseIndex].location && form.operationCase.cases[showCaseIndex].location.length) ? form.operationCase.cases[showCaseIndex].location.join("/") : '' }}
              </a-row>

              <a-row class="mt-10">
                <span class="input-tag">: </span>
                <span v-if="!form.operationCase.cases[showCaseIndex].space">
                  
                </span>
                <span v-else>
                  {{ form.operationCase.cases[showCaseIndex].spaceType === 'aboveGround' ? ' ' : ' ' }}
                  {{ form.operationCase.cases[showCaseIndex].space }}
                  <span v-if="form.operationCase.cases[showCaseIndex].spaceType === 'aboveGround'">  m </span><span v-else>  m </span>
                </span>
              </a-row>

              <a-row class="mt-10">
                <span class="input-tag">: </span>
                {{ (form.operationCase.cases[showCaseIndex].itemTypeList && form.operationCase.cases[showCaseIndex].itemTypeList.length) ? form.operationCase.cases[showCaseIndex].itemTypeList.join("/") : '' }}
              </a-row>

              <a-row class="mt-10">
                <h2 style="font-weight: bolder"></h2>
                {{ form.operationCase.cases[showCaseIndex].introduction ? form.operationCase.cases[showCaseIndex].introduction : '' }}
              </a-row>

              <a-row class="mt-10">
                <h2 style="font-weight: bolder">/</h2>
                <a-col :span="24">
                  <div v-if="form.operationCase.cases[showCaseIndex].otherPicList && form.operationCase.cases[showCaseIndex].otherPicList.length">
                    <a-col style="margin-bottom: 10px" :span="24" v-for="(pic, index) of form.operationCase.cases[showCaseIndex].otherPicList" :key="'otherPicList' + index">
                      <div @click="handlePreview(pic)" class="upload-add pic-block" style="margin: auto;display: flex; justify-content: center">
                        <img style="width: 400px; height: 400px; padding: 8px" :src="pic.thumbUrl" />
                      </div>
                      <div style="text-align: center; width: 400px;margin: auto">
                        {{ pic.description ? pic.description : '' }}
                      </div>
                    </a-col>
                  </div>
                  <div v-else>
                    
                  </div>
                </a-col>
              </a-row>
            </div>

              </div>
              <div v-else>
                
              </div>
            </a-col>

            <a-col v-if="form.plan && form.plan.status" class="mt-20">
              <h2 style="font-weight: bolder"></h2>
                <div v-if="form.plan && form.plan.status" style="border: solid 1px #e8e8e8; border-radius: 5px; padding: 10px 20px">

                <a-row class="mt-10">
                  <h2 style="font-weight: bolder"></h2>
                  {{ form.plan.description ? form.plan.description : '' }}
              </a-row>

              <a-row class="mt-10">
                <h2 style="font-weight: bolder"></h2>
                <div v-if="(form.plan.coverPicList && form.plan.coverPicList.length)" @click="handlePreview({uuid: form.plan.coverPicList[0].response.data.uuid})" class="upload-add" style="display: flex; justify-content: center">
                  <img style="max-width: 100px; max-height: 100px; padding: 8px" :src="picBaseURL + form.plan.coverPicList[0].response.data.uuid" />
                </div>
                <div v-else>
                  
                </div>
              </a-row>

              <a-row class="mt-10">
                <h2 style="font-weight: bolder">/</h2>
                <a-col :span="24">
                  <div style="margin-bottom: 10px" v-for="(pic, index) of form.plan.otherPicList" :key="'otherPicList' + index">
                    <div @click="handlePreview(pic)" class="upload-add pic-block" style="margin: auto;display: flex; justify-content: center">
                      <img style="width: 400px; height: 400px; padding: 8px" :src="pic.thumbUrl" />
                    </div>
                    <div style="text-align: center; width: 400px;margin: auto">
                      {{ pic.description ? pic.description : '' }}
                    </div>
                  </div>
                </a-col>
              </a-row>
              </div>
              <div v-else>
                
              </div>
            </a-col>

            <a-col class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <div class="mt-10">
                <span style="margin-left: 22px"></span><span style="margin-left: 22px"></span>
                {{ matchRequirementArea }}
              </div>
              <div class="mt-10">
                    
                {{ matchRequirementCityClass }}
              </div>
              <div class="mt-10">
                
                {{ matchRequirementCityFunction }}
              </div>

              <div class="mt-10">
                
                {{ (form.matchRequirement && form.matchRequirement.specialRequirement) ? form.matchRequirement.specialRequirement : '' }}
              </div>
            </a-col>

            <a-col class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <div>
                 {{ form.smallSpace ? form.smallSpace : '' }}   {{ form.largeSpace ? form.largeSpace : '' }} 
              </div>
            </a-col>

            <a-col class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <div>
                 {{ form.lessInvestment ? form.lessInvestment : '' }}   {{ form.largeInvestment ? form.largeInvestment : '' }} 
              </div>
            </a-col>

            <a-col class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <div>
                {{ form.advantage ? form.advantage : '' }}
              </div>
            </a-col>

            <a-col class="mt-20">
              <h2 style="font-weight: bolder"></h2>
              <div>
                {{ form.cooperationRequirement && form.cooperationRequirement.length ? form.cooperationRequirement : '' }}
              </div>
              <div>
                {{ form.benefitRequirement && form.benefitRequirement.length ? form.benefitRequirement : '' }}
              </div>
              <div>
                {{ form.otherCooperationRequirement && form.otherCooperationRequirement.length ? form.otherCooperationRequirement : '' }}
              </div>
            </a-col>

            <a-row v-if="!userInfo.isMembership" class="mt-20" style="text-align: center">
              <span style="color: #a1a1a1">  {{ 5 - (userInfo.free_resources ? userInfo.free_resources.length : 0) }}<span style="color: #40a9ff; cursor: pointer"></span>  </span>
            </a-row>

            <a-row style="color: #a1a1a1" class="mt-20">
              
            </a-row>
            <a-row>
              <div style="color: #a1a1a1" v-if="!itemBaseInfo.interestedUserList || !itemBaseInfo.interestedUserList.length">
                
              </div>
              <template v-for="(interest, index) in itemBaseInfo.interestedUserList">
                <img :key="'interest' + index" class="mt-10" :src="interest.Icon ? picBaseURL + interest.Icon : '/static/imgs/default.jpeg'" style="margin-right: 10px;width: 40px;height: 40px;border-radius: 20px"/>
              </template>
            </a-row>
          </div>

          <div v-if="selectedTag === 'review'">
            <a-empty v-show="!histories || !histories.length" />
            <div style="margin-top: 10px" v-for="(history, index) of histories" :key="'history' + index">
              <div v-if="history.Type !== 'audit'">
                <div>
                  <span>: {{ history.UserName }}</span><span class="ml-10">{{ history.DisplayTime }}</span>
                </div>
                <div>: {{ history.EditReason }}</div>
              </div>
              <div v-else>
                <div>
                  <span>{{ AUDIT_STATUS_2_CN[history.AuditStatus] }}</span>
                  <span class="ml-10">{{ history.UserName }}</span>
                  <span class="ml-10">{{ history.DisplayTime }}</span>
                </div>
                <div>
                  {{ history.NormalAudit }}
                </div>
                <div>
                  {{ history.ProAudit }}
                </div>
              </div>
            </div>
          </div>
        </a-col>
        <a-col :span="6"></a-col>
      </a-row>

    </a-col>

<!--    modal begin-->
    <a-modal width="80%" :visible="previewVisible" :footer="null" @cancel="previewVisible = false">
      <img style="width: 100%" :src="previewImage" />
    </a-modal>

    <a-modal
      v-model="consultModalVisible"
      :maskClosable="false"
      :closable="false"
      okText=""
      :confirm-loading="loading"
      @ok="createConsult"
    >
      <a-row>
        <a-col :span="24">
          
        </a-col>
        <a-col :span="12" class="mt-10">
          <a-input :disabled="true" v-model="consultInfo.name" style="width: 70%"></a-input>
        </a-col>
        <a-col :span="12" class="mt-10">
          <a-input :disabled="true" v-model="consultInfo.phone" style="width: 70%"></a-input>
        </a-col>
        <a-col :span="12" class="mt-10">
          <a-input :disabled="true" v-model="consultInfo.company" style="width: 70%"></a-input>
        </a-col>
        <a-col :span="12" class="mt-10">
          <a-input :disabled="true" v-model="consultInfo.position" style="width: 70%"></a-input>
        </a-col>
        <a-col :span="24" class="mt-10">
          <div>
            
          </div>
          <a-textarea v-model="consultInfo.desc"></a-textarea>
        </a-col>
        <a-col :span="24" class="mt-10">
          
        </a-col>
      </a-row>
    </a-modal>
<!--    modal end-->
    <membership @closeMembershipModal="membershipVisible = false" :membershipVisible="membershipVisible"></membership>
  </a-row>
</template>

<script>
import api from '@system/api/industryResource'
import consultApi from '@system/api/consult'
import utils from '@/utils/utils'
import {mapActions, mapGetters} from 'vuex'
import historyApi from '@system/api/history'
import { AUDIT_STATUS_2_CN } from '../../../../utils/constants'
import membership from '../components/membership'

const cityClassOptions = [
  '',
  '',
  '/',
  '',
  '',
  ''
]

const cityFunctionOptions = [
  '',
  '',
  '',
  '',
  ''
]

const areaOptions = [
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  ''
]

export default {
  name: 'industryResourceDetail',
  components: {
    membership
  },
  data () {
    return {
      loading: false,
      membershipVisible: false,
      histories: [],
      picBaseURL: '',
      AUDIT_STATUS_2_CN: AUDIT_STATUS_2_CN,
      consultModalVisible: false,
      form: {
        itemTypeList: [],
        functionOrClassList: [],
        cooperationFormList: []
      },
      consultInfo: {
        resourceType: 'industryResource',
        resourceID: '',
        name: '',
        company: '',
        desc: '',
        phone: '',
        position: ''
      },
      previewImage: '',
      areaOptions: areaOptions,
      cityClassOptions: cityClassOptions,
      cityFunctionOptions: cityFunctionOptions,
      previewVisible: false,
      selectedTag: 'basic',
      showCaseIndex: null,
      itemBaseInfo: {
        updatedAt: '',
        title: '',
        liked: false,
        recommendation: 0,
        coverPicUuid: '',
        interestedUserList: []
      }
    }
  },
  computed: {
    ...mapGetters(['userInfo']),
    'matchRequirementArea' () {
      let res = []
      if (!this.form.matchRequirement || !this.form.matchRequirement.areaList) return ''
      for (let item of this.form.matchRequirement.areaList) {
        if (this.areaOptions.includes(item)) {
          res.push(item)
        }
      }
      if (!res.length) return ''
      return res.join('/')
    },
    'matchRequirementCityClass' () {
      let res = []
      if (!this.form.matchRequirement || !this.form.matchRequirement.areaList) return ''
      for (let item of this.form.matchRequirement.areaList) {
        if (this.cityClassOptions.includes(item)) {
          res.push(item)
        }
      }
      if (!res.length) return ''
      return res.join('/')
    },
    'matchRequirementCityFunction' () {
      if (!this.form.matchRequirement || !this.form.matchRequirement.areaList) return ''
      let res = []
      for (let item of this.form.matchRequirement.areaList) {
        if (this.cityFunctionOptions.includes(item)) {
          res.push(item)
        }
      }
      if (!res.length) return ''
      return res.join('/')
    },
    'cooperationFormListComputed' () {
      let res = ''
      if (this.form.cooperationFormList && this.form.cooperationFormList.length) {
        for (let cooperationForm of this.form.cooperationFormList) {
          if (res && res[res.length - 1] !== ' ' && !cooperationForm.includes('+')) {
            res += '/'
          }
          res += cooperationForm
        }
      }

      if (!res) res = ''
      return res
    }
  },
  async mounted () {
    await this.getUserInfo()
    let flag = true
    if (!this.userInfo.isMembership && this.userInfo.free_resources && this.userInfo.free_resources.length >= 5) {
      flag = false
      for (let resource in this.userInfo.free_resources) {
        if (resource[0] === this.$route.params.id) {
          flag = true
        }
      }
    }
    if (flag) {
      this.init()
    } else {
      // 
      this.$error('5/')
      this.membershipVisible = true
    }
  },
  methods: {
    ...mapActions(['getUserInfo']),
    init () {
      this.picBaseURL = process.env.API_ROOT + '/system/pics/temp/'
      this.consultInfo.resourceID = this.$route.params.id - ''
      api.getIndustryResource(this.$route.params.id).then((res) => {
        this.form = JSON.parse(res.data.data.stringify)
        this.itemBaseInfo.updatedAt = res.data.data.industryResource.updatedAt
        this.itemBaseInfo.title = res.data.data.industryResource.title
        this.itemBaseInfo.recommendation = res.data.data.industryResource.recommendation
        this.itemBaseInfo.coverPicUuid = res.data.data.industryResource.coverPicUuid
        this.itemBaseInfo.liked = res.data.data.industryResource.liked
        this.itemBaseInfo.interestedUserList = res.data.data.industryResource.interestedUserList
        if (this.form.operationCase.cases && this.form.operationCase.cases.length !== 0) {
          this.showCaseIndex = 0
        }
      }).catch(res => {
        this.$router.go(-1)
      })
      if (this.userInfo && this.userInfo.certificate) {
        if (this.userInfo.certificate.name) {
          this.consultInfo.name = this.userInfo.certificate.name
        }
        if (this.userInfo.certificate.company) {
          this.consultInfo.company = this.userInfo.certificate.company
        }
        if (this.userInfo.certificate.position) {
          this.consultInfo.position = this.userInfo.certificate.position
        }
      }
      this.getHistory()
    },
    getHistory () {
      historyApi.getHistories({
        'resourceType': 'industryResource',
        'resourceID': this.$route.params.id
      }).then(res => {
        this.histories = res.data.data.resourceHistories
      })
    },
    createConsult () {
      this.loading = true
      consultApi.createConsult(this.consultInfo).then(res => {
        this.$success('')
        this.consultModalVisible = false
        this.loading = false
      })
    },
    showConsultModal () {
      this.consultModalVisible = true
    },
    likeResource () {
      this.loading = true
      api.likeIndustryResource(this.$route.params.id).then(res => {
        if (this.itemBaseInfo.liked) {
          this.$success('')
          this.loading = false
        } else {
          this.$success('')
          this.loading = false
        }
        this.itemBaseInfo.liked = !this.itemBaseInfo.liked
      })
    },
    async handlePreview (file) {
      if (!file.uuid && !file.preview && file.response.data.status === 'temp') {
        file.preview = await utils.getBase64(file.originFileObj)
      }
      this.previewImage = (process.env.API_ROOT + '/system/pics/temp/' + file.uuid + '/') || file.preview
      this.previewVisible = true
    },
    showCase (index) {
      this.showCaseIndex = index
    }
  }
}
</script>

<style scoped>
.selected {
  color: #40a9ff;
}

.pic-block {
  height: 400px;
  width: 400px;
  justify-content: center;
  cursor: pointer;
  float: none;
}
</style>



